code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/*
=============== 
Variables
===============
*/
:root {
  --white: #fff;
  --ps: #003087;
  --xbox: #107c10;
  --ns: #db081a;
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: var(--clr-grey-10);
  color: var(--clr-grey-1);
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: inherit;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
}
h2,
h3,
h4 {
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 2rem;
}
h2 {
  font-size: 2rem;
  margin-left: 30px;
}
h3 {
  font-size: 1.25rem;
}
p,
span,
h4 {
  font-size: 1rem;
  font-weight: 700;
}

.back {
  position: absolute;
  margin: 20px;
  font-size: 3rem;
}
.back:hover {
  color: var(--clr-primary-5);
  padding-left: 1rem;
  transition: var(--transition);
}
/*
=============== 
Navbar
===============
*/
nav {
  background: var(--clr-white);
  box-shadow: var(--light-shadow);
}
.nav-logo h1 {
  font-size: 1.5rem;
}
.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
}
.nav-header a:active {
  color: #222;
}
.nav-toggle {
  font-size: 2.5rem;
  color: var(--clr-primary-5);
  background: transparent;
  border-color: transparent;
  transition: var(--transition);
  cursor: pointer;
}
.nav-toggle p {
  font-size: 2.5rem;
}

@media only screen and (min-width: 950px) {
  .nav-toggle {
    display: none;
  }
  .nav-logo h1 {
    font-size: 2rem;
  }
}
.nav-toggle:hover {
  color: var(--clr-primary-1);
  overflow: hidden;
} /*
transform: rotate(90deg);
background-color: rgba(139, 148, 145, 0.596);*/
.links a {
  color: var(--clr-grey-3);
  font-size: 1rem;
  text-transform: capitalize;
  letter-spacing: var(--spacing);
  display: block;
  padding: 0.5rem 1rem;
  transition: var(--transition);
  font-weight: 500;
}
.links a:hover {
  background: var(--clr-primary-8);
  color: var(--clr-primary-5);
  padding-left: 2rem;
}
.social-icons {
  display: none;
}
.links-container {
  height: 0;
  overflow: hidden;
  transition: var(--transition);
}
.show-container {
  height: 10rem;
}
/*
=============== 
Filter
===============
*/
.filter {
  max-width: 200px;
  position: fixed;
  left: 30px;
  margin-top: 80px;
}
.filter_canvas h3 {
  font-size: 1.5rem;
  left: 80px;
  position: fixed;
}
.filter a {
  color: var(--clr-grey-3);
  font-size: 1rem;
  text-transform: capitalize;
  letter-spacing: var(--spacing);
  display: block;
  padding: 0.5rem 1rem;
  transition: var(--transition);
  font-weight: 600;
}
.filter a:hover {
  background: var(--clr-primary-9);
  color: var(--clr-black);
  padding-left: 2rem;
}
@media only screen and (max-width: 950px) {
  .filter_canvas h3 {
    display: none;
  }
  .filter {
    display: none;
  }
}
/*
=============== 
Home
===============
*/
.home_content h2 {
  left: 0%;
}
.home_content2 {
  max-width: 90%;
  margin: auto;
}
@media only screen and (min-width: 950px) {
  .home_content h2 {
    left: 0%;
    display: none;
  }
}
/*
=============== 
GameList
===============
*/
.game-section {
  width: 80%;
  padding-left: 20%;
}
.game-section input {
  width: 50%;
  padding-left: 20%;
  margin: 10px;
  padding: 10px;
}
.search {
  margin: 10px;
  padding: 10px;
  padding-left: 20px;
  background-color: var(--clr-white);

  align-items: center;
  display: flex;
}

.search i {
  padding-top: 5px;
  font-size: 2rem;
}
@media only screen and (max-width: 950px) {
  .game-section {
    width: 100%;
    padding-left: 0%;
  }

  .game-section input {
    width: 65%;
    margin: 0px;
    margin-left: 0px;
    padding: 10px;
    padding-left: 0px;
  }
}
/*
=============== 
SingleGame
===============
*/
.singleGame {
  margin: 10px;
  padding: 10px;
  padding-left: 20px;
  background-color: var(--clr-white);
}
.singleGame span {
  margin-left: 30px;
  text-transform: uppercase;
}
.singleGame p {
  margin: 0;
}
.singleGame h3 {
  margin: 0;
  margin-bottom: 5px;
}
.xbox {
  color: var(--xbox);
}

.ps {
  color: var(--ps);
}

.ns {
  color: var(--ns);
}
/*
=============== 
Game
===============
*/
.Game_Page {
  margin: auto;
  margin-top: 30px;
  width: 70%;
  background-color: var(--clr-white);
}
.game_canvas {
  width: 50%;
  min-height: 400px;
  padding: 30px;
  margin: auto;
}
.game_canvas h3 {
  font-size: 3rem;
}
.game_canvas span {
  font-size: 1.5rem;
}
.game_canvas p {
  margin-left: 5px;
  margin-right: 10px;
  font-size: 1rem;
}
.game_information {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid var(--clr-primary-5);
}
.game_information_some {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media only screen and (max-width: 950px) {
  .Game_Page {
    width: 90%;
  }
  .game_canvas {
    width: 80%;
  }
}
/*
=============== 
AddGame
===============
*/
.add_main h2 {
  text-align: center;
}
.add_main_form_part {
  width: 70%;
  margin: auto;
  padding: 30px;
  min-height: 300px;
}
.add_main_content_part {
  justify-content: center;
  display: flex;
}
.add_main_content_part_block1 {
  padding-right: 10%;
}
.add_main_content_part_block2 {
  padding-left: 10%;
  border-left: 2px solid rgba(138, 138, 138, 0.616);
}
.add_main_content_part_block2 input {
  border-radius: 10px;
  padding: 5px;
  margin: 5px;
}
.add_main_content_part_block1 input {
  width: 100%;
  padding: 5px;
  margin-bottom: 8px;
}

.add_main_form_part select {
  width: 100%;
  padding: 5px;
  border-radius: 10px;
  margin-bottom: 8px;
}

.add_main_form_part textarea {
  padding: 5px;
  margin-bottom: 8px;
  resize: none;
}
.Game_Page_warning div {
  color: #e60012;
  border-radius: 10px;
  padding: 5px;
  margin: 5px;
}
@media only screen and (max-width: 950px) {
  .add_main_content_part {
    flex-direction: column;
  }
  .add_main_content_part_block1 {
    padding-right: 0%;
  }
  .add_main_content_part_block2 {
    padding-left: 0%;
    border-left: none;
  }
}
